.App{
    height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
    display: grid;
  position: relative;
}

Canvas{
    z-index: 1;
    height: 100vh;
    width: 100%;
    position: relative;
    cursor:pointer;
}

.navigation{
    z-index: 2;
    position: absolute;
    text-align: center;
    background-color: white;
    width: auto;
    padding: 0.5rem;
    
}



.background{
    position: fixed;
    z-index: 3;
    background-color: rgba(0, 14, 31, 0.8);
    height: 100vh;
    width: 100vw;
}

.PopUp{
    position: fixed;
    z-index: 4;
    justify-content: center;
    text-align: center;
    padding: 2rem;
    display:flex;
    flex-direction: column;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 40rem;
   
}

.content{
    z-index: 5;
    color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    margin: 10%
   
}

.welcome{
    order: 0;
    
}

.description{
    order: 1;
    margin: 3rem;
}

.exit{
    cursor: pointer;
    z-index: 5;
    color: rgb(0, 37, 80);
    justify-content: center;
    background-color: rgb(255, 255, 255);
    text-align: center;
    padding: 1rem;
    width: 12rem;
    border-radius: 1rem;
    margin: auto;
    order: 2;
}